.wp-block-fim-parish-info-parish-contact {

	.contact-container{
		display: flex;
		flex-wrap: wrap;
		gap: 2rem;

		> div{
			position: relative;
			flex: calc(50%  - 2rem);
		}
	}


	.parish-info-contact-wrap{

		h3{
			font-size: var(--wp--preset--font-size-medium);
			margin-bottom: 1em;
		}

	}

	.parish-info-map{
		min-width: 280px;
		min-height: 200px;

		.mapouter{
			width: 100%;
			height: 100%;
			position: relative;

			#gmap_canvas{
				position: absolute;
				top:0;
				left:0;
				width: 100%;
				height: 100%;
			}


		}
	}
}

.components-panel__row{
	 flex-flow: unset;
}
